.navbar {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  border-radius: 0 5px 5px 0;
  background-color: white;
  list-style-type: none;
  width: 60px;
  padding: 0 10px 0 10px; /* Total Width is 80px*/
  z-index: 99; /* forces navbar to be on top */

  /* this makes it so you can't highlight this object */
  -webkit-user-select: none; /* Webkit */ /* Firefox */
  -ms-user-select: none; /* IE 10 */

  /* Currently not supported in Opera but will be soon */
  -o-user-select: none;
  user-select: none;

  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  /*************/
}

.navbar > ul {
  padding: 0%;
}

.navbar-icon {
  margin-bottom: 15px;
  padding-bottom: 5px;
  width: 60px;
}

/* make navbar bottom padding same as border */
.navbar-icon:hover {
  border-bottom: 5px solid black;
  padding-bottom: 0;
}

#summary-page {
  overflow: hidden;
}

#date-block {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: fixed;
  right: 0;
  top: 0;
  display: block;
  color: black;
  margin-top: 0;
  background-color: white;
  padding: 25px 100px 25px 25px;
  border-radius:  0 0 15px 15px;
  box-shadow: 0 0 25px black;
}

#mood-picker {
  background-color: salmon;
  width: 500px;
  border: 2px dotted white;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 25px;

  padding-left: 25px;
  padding-right: 25px;
  padding-bottom: 25px;
  box-shadow: 0 0 25px black;
  border-radius: 5px;

  /* this makes it so you can't highlight this object */
  -webkit-user-select: none; /* Webkit */ /* Firefox */
  -ms-user-select: none; /* IE 10 */

  /* Currently not supported in Opera but will be soon */
  -o-user-select: none;
  user-select: none;

  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  /*************/
}

#mood-picker > p {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 20px;
}

#mood-picker > ul {
  padding: 0;
  margin: 0;
}

#mood-picker > ul > li {
  list-style-type: none;
  margin: 5px;
  font-size: 48px;
  width: 72px;       /* needed to freeze components in place */
  line-height: 40px; /* needed to freeze components in place */
  display: inline-block;
  vertical-align: middle;
  transition: 0.3s;
}

#mood-picker > ul > li:hover {
  font-size: 72px; /* same value as width */
}

#adviceDisplay {
  border-radius: 50px 0 0 0;
  position: absolute;
  background-color: turquoise;
  bottom: 0;
  right: 0;
  width: 80%;
  max-height: 150px;
  box-shadow: 0 0 50px black;

  /* this makes it so you can't highlight this object */
  -webkit-user-select: none; /* Webkit */ /* Firefox */
  -ms-user-select: none; /* IE 10 */

  /* Currently not supported in Opera but will be soon */
  -o-user-select: none;
  user-select: none;

  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  /*************/
}

#adviceDisplay > a {
  color: purple;
  line-height: 3;
  overflow-x: hidden;
  white-space: nowrap;
}

#advice-text-block {
  border-radius: 50px 0 0 0;
  background-color: lightblue;
  color: black;
  font-style: oblique;
  padding: 15px 50px 15px 50px; /* top right bottom left */
}

.calendar {
  background-color: rgb(63, 133, 157);
  border: 5px solid black;
  border-radius: 30px;
  padding: 30px 50px 50px 50px;
  display: inline-block;
  vertical-align: inherit;
  width: -webkit-max-content;
  width: max-content;

  /* this makes it so you can't highlight this object */
  -webkit-user-select: none; /* Webkit */ /* Firefox */
  -ms-user-select: none; /* IE 10 */

  /* Currently not supported in Opera but will be soon */
  -o-user-select: none;
  user-select: none;

  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  /*************/
}

.calendar-top {
  color: inherit;
}

/* For the arrow buttons on Calendar */
.arrow-button {
  font-size: 40px;
  font-weight: bold;
  background-color: white;
  width: 75px;
  border-radius: 5px;
  vertical-align: super;
}

/* For Month and Year Appearance */
#month-year-text {
  font-family:'Times New Roman', Times, serif;
  font-size: 36pt;
  display: inline-block;
  width: 390px;
  margin: 3%;
  text-decoration: underline;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  -webkit-text-decoration-style: solid;
          text-decoration-style: solid;
  -webkit-text-decoration-color: currentcolor;
          text-decoration-color: currentcolor;
  text-decoration-thickness: auto;
  -webkit-text-decoration-color: brown;
          text-decoration-color: brown;
}

/* For Day of Week Text "Su", etc. */
.day-week-text {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: x-large;
  text-decoration: underline;
}

/* generic calendar tile */
.tile {
  height: 75px;
  width: 75px;
  outline: 1px solid black;
  display: table-cell;
  position: relative;
  background-color: lavender;
  border-radius: 3px;
  vertical-align: middle;
  transition: 0.1s;
}

/* Makes an outline appear when hovering over a tile */
.tile:hover {
  outline: 5px solid rgb(70, 70, 70);
}

.tile-num {
  text-align: center;
  font-size: 36px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: black;
}

.tile-mood {
  position: absolute;
  font-size: 30px;
  right: 0;
  top: 42px;
}

/* selected tile will have a different color */
.selected-tile {
  background-color: lightgreen;
}

/* selected tile will have a different color */
.inactive-tile {
  background-color: gray;
}

#page-contents {
  vertical-align: middle;
}

#page-contents>div {
  margin-top: 50px;
}

#day-info-block {
  vertical-align: inherit;
  display: inline-block;
  width: 500px;
  margin-left: 50px;
}

#day-title {
  background-color: teal;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 20px;
  padding: 40px;
  border-radius: 50px;
}

#day-title>p {
  height: 30px;
  margin: 15px 5px 5px 5px;
  font-size: 30px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: bolder;
}

#daily-breakdown {
  background-color: rgb(114, 155, 155);
  padding: 20px;
  text-align: left;
  border-radius: 20px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: bold;
  font-size: 14pt;
}

/* necessary because emojis change line height */
#daily-breakdown>p {
  line-height: 20px !important;
}
